.song {
  grid-area: song;
  display: flex;
  flex-direction: column;
  align-items: center;
  font-family: "Open Sans";

  .song__image {
    width: 10rem;
    height: 10rem;
    margin: 0 2rem;
    border-radius: 50px;
    background: url("../images/unreleased_cover.png");
    background-size: cover;
    background-position: center;
    z-index: 50;
  }

  .song__title {
    font-size: 3rem;
    color: white;
    margin-top: 0.8rem;
    font-weight: 800;
  }

  .song__author {
    font-size: 2rem;
    color: lighten($color, 40);
    opacity: 0.5;
  }
}

@media screen and (max-width: 425px) {
  .song {
    justify-content: center;
    .song__image {
      width: 10rem;
      height: 10rem;
    }

    .song__title {
      font-size: 2rem;
    }

    .song__author {
      font-size: 1rem;
    }
  }
}
