* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  width: 100vw;
  height: 100vh;

  background: url("../images/bg_image.jpg");
  background-size: cover;
  background-position: top;
  background-color: $color;
  font-family: "Open Sans";

  .container {
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-rows: 20% 35% 20% 15% 10%;
    grid-template-areas:
      "header"
      "song"
      "buttons"
      "progres"
      "footer";
  }

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;

    background: linear-gradient(
      to top,
      rgba(33, 28, 54, 1),
      rgba(33, 28, 54, 1),
      rgba(33, 28, 54, 0.8)
    );
    z-index: -1;
  }
}

a {
  text-decoration: none;
}
img {
  width: 100%;
}
