.header {
  grid-area: header;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 0.5rem;
  .album {
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    font-family: "Open Sans";
    margin: 0 40rem;
    margin-top: 3rem;

    span:first-child {
      font-size: 2rem;
      color: lighten($color, 30);
      opacity: 0.9;
    }

    span:nth-of-type(2) {
      font-size: 2.5rem;
      color: white;
    }
  }
}

.back {
  width: 1.5rem;
  height: 1.5rem;
  z-index: 200;
  margin-top: 2rem;
  margin-left: 4rem;
}

.header__menu {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 3rem;
  height: 3rem;
  margin-top: 2rem;
  margin-right: 4rem;
  z-index: 150;
  cursor: pointer;
  transition: 2s;

  div {
    width: 5px;
    height: 5px;
    border-radius: 50px;
    background: white;
  }
}

@media screen and (max-width: 425px) {
  .header {
    grid-area: header;
    display: flex;

    justify-content: space-around;
    align-items: center;

    .back {
      width: 0.8rem;
      height: 0.8rem;
      align-self: flex-start;
    }

    .album {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: flex-start;

      margin: 0;
      margin-top: 0;
      span:first-child {
        font-size: 1rem;
      }

      span:nth-of-type(2) {
        font-size: 1.5rem;
        color: white;
      }
    }

    .header__menu {
      width: 1rem;
      height: 1rem;
      align-self: flex-start;

      div {
        width: 3px;
        height: 3px;
        border-radius: 50px;
        background: white;
      }
    }
  }
}
